@use '../../../node_modules/@wordpress/base-styles/colors' as colors;
@use '../../../node_modules/@wordpress/base-styles/variables' as vars;
@use '../../../node_modules/@wordpress/base-styles/mixins' as mixins;
@use '../../../node_modules/@wordpress/base-styles/breakpoints' as breakpoints;
@use '../../../node_modules/@wordpress/base-styles/animations' as animations;
@use 'mixins' as admin_mixins;

@include mixins.wordpress-admin-schemes();

body {
  @include mixins.admin-scheme(#007cba);

  &.strt_page_strt-plugin-setting {

	#poststuff .inside h2 {
	  font-size: 18px;
	  padding: 8px 0;
	  font-weight: 500;
	  border-bottom: 1px solid #c3c4c7;
	  margin-bottom: 15px;
	}

	.strt-header {
	  text-align: center;
	  margin: -10px -20px 15px -20px;
	  background: #fff;
	}

	.strt-title {
	  display: flex;
	  align-items: start;
	  justify-content: start;
	  clear: both;
	  padding-top: 8px;
	  padding-left: 20px;
	}

	.nav-tab-wrapper {
	  display: flex;
	  flex-wrap: wrap;
	  list-style: none;
	  padding-left: 20px;
	  gap: 5px;

	  a {
		float: none;
		margin-left: 0;
		border: 0;
		background: none;
		font-weight: 510;
		font-size: 14px;
		color: #505050;
		text-decoration: none;

		&:focus, &:hover, &:active {
		  outline: none;
		  box-shadow: none;
		}
	  }

	  margin: 0;
	  background: #FFF;
	  border-bottom: 1px solid #EBEBEB;

	  .nav-tab-active, .nav-tab-active:focus, .nav-tab-active:focus:active, .nav-tab-active:hover {
		border-bottom: 2px solid var(--wp-admin-theme-color, #3858e9);
		color: #070707;
	  }

	  .nav-tab:hover,
	  .nav-tab:focus {
		color: var(--wp-components-color-accent, var(--wp-admin-theme-color, #3858e9));
	  }
	}



	.file-delete-button,
	.image-delete-button {
	  color: #d63638 !important;
	  border: 1px solid #d63638 !important;

	  &:focus,
	  &:hover,
	  &:active {
		color: #b92c2d !important;
		border: 1px solid #b92c2d !important;
		box-shadow: none;
	  }
	}

	.hidden {
	  display: none !important;
	}

	.show {
	  display: block !important;
	}
  }
}